
import { useStore } from "@/store";
import { computed, defineComponent } from "vue";

export default defineComponent({
  name: "GameStatistics",
  setup: () => {
    const { state } = useStore();
    const { trainingDayNumber, lastResult, accuracy } = computed(() => state.statistics).value;

    return {
      trainingDayNumber,
      lastResult,
      accuracy,
    };
  },
});
